const route = {
    path: '/category',
    component: ()=>import('../pages/category/root'),
    children: [
        {
            path: 'subcate/:categoryId/:subCateId',
            component: ()=>import('../pages/category/sub-category'),
            props: true,
            children: [
                {
                    path: 'detail/:goodsId',
                    component: ()=>import('../pages/common/goods-detail'),
                    props: true
                }
            ]
        }
    ]
}

export default route;