const route = {
    path: '/home',
    component: ()=>import('../pages/home/root'),
    children: [
        {
            path: 'cate/:cateId',
            component: ()=>import('../pages/home/category-list'),
            props: true,
            children: [
                {
                    path: 'detail/:goodsId',
                    component: ()=>import('../pages/common/goods-detail'),
                    props: true
                }
            ]
        },
        {
            path: 'detail/:goodsId',
            component: ()=>import('../pages/common/goods-detail'),
            props: true
        }
    ]
}

export default route;