import {api, request} from '../../ajax'

const state = {
    topicData: {
        hasMore: true,
        list: []
    },
    showPreview: false,
    previewInfo: {
        listIndex: null,
        imgIndex: null
    }
};
const mutations = {
    setTopicList(state, param){
        let list = [];
        if(param.type === 'loadmore'){
            list = [...state.topicData.list, ...param.list];
        }else if(param.type === 'refresh' || param.type === 'init'){
            list = param.list;
        }
        state.topicData = {
            hasMore: param.hasMore,
            list
        }
    },
    setShowPreview(state, param){
        // 是否预览
        if(param.show){
            // 需要预览，取出下标
            state.previewInfo.listIndex = param.listIndex;
            state.previewInfo.imgIndex = param.imgIndex;
        }else{
            state.previewInfo.listIndex = null;
            state.previewInfo.imgIndex = null;
        }
        state.showPreview = param.show;
    }
}
const actions = {
    // 请求数据
    async getTopicListAction(context, {id, size, page, type}){
        let result = await request.get(api.TOPIC_LIST_API, {tabId: id, size, page});
        console.log(result);
        let hasMore = result.data.data.hasMore;
        let newData = result.data.data.result.map(({
            topicId,
            type,
            title,
            subTitle,
            picUrl,
            readCount,
            avatar,
            nickname,
            itemList
        })=>{
            let newList = [];
            if(itemList){
                // 处理上新的商品数据
                let length = Math.floor(itemList.length / 4 ) * 4;
                newList = itemList.splice(0, length);
            }
            return {
                id: topicId,
                type,
                title,
                subTitle,
                picUrl,
                readCount,
                avatar,
                nickname,
                itemList: newList
            }
        });
        console.log(hasMore);
        context.commit('setTopicList', {hasMore, list: newData, type});
    }
}


export default {
    namespaced: true,
    state,
    mutations,
    actions
}